.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.search-form {
  background-color: #1f1f8a;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
  padding: 12px;
  display: flex;
}

.search-form input {
  width: 500px;
  height: 40px;
  border: none;
  border-radius: 4px;
  padding-left: 10px;
  font-size: 20px;
}

.search-form button {
  cursor: pointer;
  background-color: #add8e6;
  border: none;
  border-radius: 4px;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 20px;
}

.load-more {
  cursor: pointer;
  background-color: #add8e6;
  border: none;
  border-radius: 4px;
  margin: 50px auto;
  padding: 15px;
  font-size: 20px;
  display: flex;
}

.photo-card {
  background-color: #cdfafa;
  border-radius: 10px;
  padding: 10px;
}

.photo-card img {
  width: 400px;
  height: 250px;
  object-fit: cover;
  border-radius: 8px;
}

.info {
  text-align: center;
  background-color: #a5e6e6;
  border-radius: 8px;
  padding-top: 12px;
  padding-bottom: 10px;
  font-size: 16px;
}

.hidden {
  display: none;
}

